import React, { createContext, useState, useEffect, useCallback } from 'react';
// We can't use types directly in a .js file, but we can document them with JSDoc

/**
 * @typedef {Object} User
 * @property {string} [id]
 * @property {string} [username]
 * @property {string} [email]
 * @property {string} [firstName]
 * @property {string} [lastName]
 * @property {'admin'|'agency_admin'|'agency_staff'|'citizen'} [role]
 */

/**
 * @typedef {Object} AuthContextType
 * @property {User|null} currentUser
 * @property {boolean} loading
 * @property {function(User, string):void} login
 * @property {function():void} logout
 * @property {function():boolean} isSuperAdmin
 * @property {function():boolean} isAuthenticated
 * @property {function():Promise<User|null>} validateToken
 */

/** @type {React.Context<AuthContextType>} */
export const AuthContext = createContext({
  currentUser: null,
  loading: true,
  login: () => {},
  logout: () => {},
  isSuperAdmin: () => false,
  isAuthenticated: () => false,
  validateToken: () => Promise.resolve(null)
});

/**
 * @param {Object} props
 * @param {React.ReactNode} props.children
 * @returns {JSX.Element}
 */
export const AuthProvider = ({ children }) => {
  const [currentUser, setCurrentUser] = useState(null);
  const [loading, setLoading] = useState(true);
  
  /**
   * Validate the stored token with the server
   * @returns {Promise<User|null>}
   */
  const validateToken = useCallback(async () => {
    try {
      const token = localStorage.getItem('token');
      
      if (!token) {
        return null;
      }
      
      const response = await fetch('http://localhost:3001/api/auth/me', {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        }
      });
      
      if (!response.ok) {
        throw new Error('Token validation failed');
      }
      
      const data = await response.json();
      return data.data.user;
    } catch (error) {
      console.error('Token validation error:', error);
      localStorage.removeItem('token');
      localStorage.removeItem('user');
      return null;
    }
  }, []);
  
  // Load user from storage when component mounts
  useEffect(() => {
    const loadUserFromStorage = async () => {
      try {
        setLoading(true);
        
        // Try to get user from localStorage
        const storedUser = JSON.parse(localStorage.getItem('user') || 'null');
        const token = localStorage.getItem('token');
        
        if (token && storedUser) {
          // Verify the token is still valid
          const validatedUser = await validateToken();
          
          if (validatedUser) {
            console.log('Loaded user from storage:', validatedUser);
            setCurrentUser(validatedUser);
          } else {
            console.log('Stored token is invalid, logging out');
            localStorage.removeItem('token');
            localStorage.removeItem('user');
            setCurrentUser(null);
          }
        } else {
          console.log('No user in storage');
          setCurrentUser(null);
        }
      } catch (error) {
        console.error('Error loading user from storage:', error);
        localStorage.removeItem('token');
        localStorage.removeItem('user');
        setCurrentUser(null);
      } finally {
        setLoading(false);
      }
    };
    
    loadUserFromStorage();
  }, [validateToken]);
  
  /**
   * Login function
   * @param {User} userData - User data
   * @param {string} token - Auth token
   */
  const login = useCallback((userData, token) => {
    console.log('Login called with:', userData);
    localStorage.setItem('token', token);
    localStorage.setItem('user', JSON.stringify(userData));
    setCurrentUser(userData);
  }, []);
  
  /**
   * Logout function
   */
  const logout = useCallback(() => {
    console.log('Logging out...');
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    setCurrentUser(null);
  }, []);
  
  /**
   * Check if user is admin
   * @returns {boolean}
   */
  const isSuperAdmin = useCallback(() => {
    return currentUser?.role === 'admin' || currentUser?.role === 'agency_admin';
  }, [currentUser]);
  
  /**
   * Check if user is authenticated
   * @returns {boolean}
   */
  const isAuthenticated = useCallback(() => {
    return !!currentUser;
  }, [currentUser]);
  
  // Log auth state changes
  useEffect(() => {
    console.log('Auth state updated:', { 
      authenticated: !!currentUser,
      user: currentUser
    });
  }, [currentUser]);
  
  const authContextValue = {
    currentUser,
    loading,
    login,
    logout,
    isSuperAdmin,
    isAuthenticated,
    validateToken
  };
  
  return (
    <AuthContext.Provider value={authContextValue}>
      {children}
    </AuthContext.Provider>
  );
};

export default AuthProvider; 